import Bmob from "hydrogen-js-sdk";
import common from '@/utils/common.js';

let uid;
let that;
export default {
	data() {
		return {
			start_date: common.getDay(0, true),
			end_date: common.getDay(1, true),
			start_date_desc: '',
			end_date_desc: '',

			headerData: {
				data1: 0,
				data2: 0,
				data3: 0,
			},
			opreaterList: [],
		}
	},

	onLoad() {
		that = this;
		uid = uni.getStorageSync("uid");

		that.start_date_desc = that.start_date.split(" ")[0];
		that.end_date_desc = that.end_date.split(" ")[0];
		that.getdetail()
	},

	methods: {

		StorageSync(item) {
			uni.setStorageSync('staff', item)
		},

		bindDate_startChange(e) {
			console.log(e)
			that.start_date = e.detail.value + ' 00:00:00';
			that.getdetail()
		},

		bindDate_endChange(e) {
			console.log(e)
			that.end_date = e.detail.value + ' 00:00:00';
			that.getdetail()
		},

		getdetail() {
			uni.showLoading({
				title: "加载中..."
			})
			const query = Bmob.Query("_User");
			const query1 = query.equalTo("objectId", '==', uid);
			const query2 = query.equalTo("masterId", '==', uid);
			query.or(query1, query2);
			query.find().then(res => {
				console.log(res)
				let opreaterList = res
				let count = 0;
				for (let item of opreaterList) {
					const query = Bmob.Query("order_opreations");
					query.equalTo("master", "==", uid);
					query.equalTo("opreater", "==", item.objectId);
					query.equalTo("type", "==", -1);
					query.equalTo("status", "==", true);
					query.equalTo("extra_type", "==", 1);
					query.equalTo("createdAt", ">=", that.start_date);
					query.equalTo("createdAt", "<=", that.end_date);
					query.count().then(res => {
						let opreaterCount = res
						count += 1
						if (opreaterCount == 0) {
							item.sellNum = 0
							item.sellPrice = 0
							if (count == opreaterList.length) {
								that.opreaterList = opreaterList
								uni.hideLoading()
							}

						} else {
							for (var i = 0; i < Math.ceil(opreaterCount / 500); i++) {
								query.limit(500);
								query.skip(500 * i);
								query.find().then(res => {
									item.sellNum = res.length > 0 ? res.length : 0
									item.sellPrice = 0
									for (let record of res) {
										item.sellPrice += record.all_money
									}

									if (count == opreaterList.length) {
										that.opreaterList = opreaterList
										uni.hideLoading()
									}

								})
							}
						}
					})
				}

				that.getheaderData()
			});
		},

		//得到首页的总数
		getheaderData() {
			that.headerData = {
				data1: 0,
				data2: 0,
				data3: 0
			};
			const query = Bmob.Query("order_opreations");
			query.equalTo("master", "==", uid);
			query.equalTo("type", "==", -1);
			query.equalTo("status", "==", true);
			query.equalTo("extra_type", "==", 1);
			query.equalTo("createdAt", ">=", that.start_date);
			query.equalTo("createdAt", "<=", that.end_date);
			query.limit(500);
			query.count().then(res => {
				let count = res
				for (var i = 0; i < Math.ceil(count / 500); i++) {
					query.limit(500);
					query.skip(500 * i);
					query.find().then(res => {
						for (let item of res) {
							that.headerData.data1 += item.all_money ? item.all_money : 0
							that.headerData.data2 += item.profit ? item.profit : 0
						}
						that.headerData.data3 += res.length
					});
				}
			})

		}
	}
}
